<script>
export default {
  name: 'ChooseGame',
  computed: {
    title() {
      let title = 'Выберите игру';

      if (String.prototype.includes.call(this.$route.name, 'noughts-crosses')) {
        title = 'Крестики-нолики';
      } else if (String.prototype.includes.call(this.$route.name, 'battleship')) {
        title = 'Морской бой';
      }

      return title;
    },
