<template>
  <v-card>
    <v-container>
      <v-row justify="space-between">
        <v-col
          cols="3"
          sm="12"
          md="3"
        >
          <v-img
            height="128"
            :src="imgSrc"
            :lazy-src="imgSrc"
          />
        </v-col>

        <v-col
          cols="9"
          sm="12"
          md="9"
          class="text-center pa-0"
        >
          <v-card-title>
            {{ title }}
          </v-card-title>

          <v-card-subtitle class="text-left">
            <p>
              {{ description }}
            </p>

            <v-btn
              text
              x-small
              color="info"
              class="pl-0 pr-0"
              @click="isGameInfoOpen = !isGameInfoOpen"
            >
              <span>Подробнее</span>

              <v-icon
                x-small
                class="ml-1"
              >
                {{ isGameInfoOpen ? 'fas fa-arrow-up' : 'fas fa-arrow-down' }}
              </v-icon>
            </v-btn>
          </v-card-subtitle>

          <v-expand-transition>
            <div v-show="isGameInfoOpen">
              <v-card-text
                v-for="(rule, index) in rules"
                :key="index"
                class="text-left pt-0 pb-0"
              >
                <h4>
                  {{ rule.title }}
                </h4>

                <p>
                  {{ rule.text }}
                </p>
              </v-card-text>
            </div>
          </v-expand-transition>

          <v-card-actions>
            <v-btn
              :to="href"
              color="accent"
            >
              Играть
            </v-btn>

            <v-spacer />
          </v-card-actions>
        </v-col>
      </v-row>
    </v-container>
  </v-card>
</template>

<script>
export default {
  name: 'AppGameCard',
  props: {
    item: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      isGameInfoOpen: false,
    };
  },
  computed: {
    imgSrc() {
      return this.item && this.item.img && this.item.img.src ? this.item.img.src : '';
    },
    title() {
      return this.item && this.item.title ? this.item.title : 'Название игры';
    },
    description() {
      return this.item && this.item.description ? this.item.description : 'Описание игры';
    },
    href() {
      return this.item && this.item.href ? this.item.href : '/';
    },
    rules() {
      return this.item && this.item.rules ? this.item.rules : [];
    },
  },
};
</script>

<style scoped>

</style>
